<template>
  <div class="login-container">
    <el-form
      ref="loginForm"
      :model="loginForm"
      :rules="loginRules"
      class="login-form"
      auto-complete="on"
      label-position="left"
    >
      <el-card class="box-card">
        <div class="title-container">
          <div class="logo" style="text-align: center">
            <img width="60%" src="@/assets/guidance/logo.png">
            <h1>LOGIN</h1>
          </div>
        </div>
        <br>
        <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane name="student" label="Student" />
          <el-tab-pane name="teacher" label="Teacher" />
          <el-tab-pane name="guidance" label="Guidance" />
        </el-tabs>
        <el-form-item prop="username">
          <span class="svg-container">
            <svg-icon icon-class="user" />
          </span>
          <el-input
            ref="username"
            v-model="loginForm.username"
            placeholder="Username"
            name="username"
            type="text"
            tabindex="1"
            auto-complete="on"
          />
        </el-form-item>

        <el-form-item prop="password">
          <span class="svg-container">
            <svg-icon icon-class="password" />
          </span>
          <el-input
            :key="passwordType"
            ref="password"
            v-model="loginForm.password"
            :type="passwordType"
            placeholder="Password"
            name="password"
            tabindex="2"
            auto-complete="on"
            @keyup.enter.native="handleLogin"
          />
          <span class="show-pwd" @click="showPwd">
            <svg-icon :icon-class="passwordType === 'password' ? 'eye' : 'eye-open'" />
          </span>
        </el-form-item>
        <el-button :loading="loading" type="primary" style="width:100%;margin-bottom:30px;" @click.native.prevent="handleLogin">Login</el-button>
        <span>
          <el-link style="float: left">Create new account</el-link>
          <el-link style="float: right">Forgot password</el-link>
        </span>
        <div style="margin-top: 20%; font-size: 14px">
          <div>Republic of the Philippines</div>
          <div style="font-weight: bold">OCCIDENTAL MINDORO STATE COLLEGE</div>
          <div>Sablayan, Occidental Mindoro</div>
          <div>Website: www.omsc.edu.ph</div>
          <div>Email address: omsc_9747@yahoo.com</div>
          <div>Tele/Fax: (043) 457-0231</div>
        </div>
      </el-card>
    </el-form>
  </div>
</template>

<script>
import { login } from '@/graphql/auth'

export default {
  name: 'Login',
  data() {
    return {
      loginForm: {
        username: '',
        password: ''
      },
      loginRules: {
        username: [{ required: true, trigger: 'blur' }],
        password: [{ required: true, trigger: 'blur' }]
      },
      loading: false,
      passwordType: 'password',
      redirect: undefined,
      activeName: 'first',
      registration_type: ''
    }
  },
  watch: {
    $route: {
      handler: function(route) {
        this.redirect = route.query && route.query.redirect
      },
      immediate: true
    }
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab.label)
      this.registration_type = tab.label
      console.log(this.registration_type)
    },
    showPwd() {
      if (this.passwordType === 'password') {
        this.passwordType = ''
      } else {
        this.passwordType = 'password'
      }
      this.$nextTick(() => {
        this.$refs.password.focus()
      })
    },
    handleLogin() {
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          this.loading = true
          login(this.loginForm, (response) => {
            if (response.login) {
              localStorage.setItem('access_token', response.login.access_token)
              localStorage.setItem('user', JSON.stringify(response.login.user))
              this.$router.push({ path: '/' })
            } else {
              this.$message({
                message: `Invalid user credentials.`,
                dangerouslyUseHTMLString: true,
                type: 'error'
              })
            }
          })
          this.loading = false
        } else {
          this.loading = false
          return false
        }
      })
    }
  }
}
</script>

